<template>
  <div class="wrap nav-center-stores">
    <div class="box">
      <div class="header-box">
        <img src="/static/imgs/temp/ 10@2x.png" alt="" />
      </div>
      <div class="box-detail stores-manage-wrap">
        <el-form :model="organization" :rules="rules">
          <el-form-item label="" :label-width="labelWidth" prop="name">
            <el-col :span="16">
              <el-input :class="{'editState': editState.nameState == false}" v-model="organization.name" :placeholder="editState.nameState ? '' : '20'" :disabled="editState.nameState"></el-input>
              <el-button v-show="editState.nameState" type="text" @click.prevent="editState.nameState = false"></el-button>
              <el-button v-show="!editState.nameState" size="mini" @click.prevent="editOrganizationName"></el-button>
            </el-col>
            <el-col :span="8">
              <div class="el-form_item-tip"></div>
            </el-col>
          </el-form-item>
        </el-form>
        <el-form :model="organization" :rules="rules">
          <el-form-item label="" :label-width="labelWidth" prop="phone">
            <el-col :span="16">
              <el-input :class="{'editState': editState.phoneState == false}" v-model="organization.phone" :placeholder="editState.phoneState ? '' : ''" :disabled="editState.phoneState"></el-input>
              <el-button v-show="editState.phoneState" type="text" @click.prevent="editState.phoneState = false"></el-button>
              <el-button v-show="!editState.phoneState" size="mini" @click.prevent="editOrganizationPhone"></el-button>
            </el-col>
            <el-col :span="8">
              <div class="el-form_item-tip"></div>
            </el-col>
          </el-form-item>
        </el-form>
        <el-form>
          <el-form-item label="" :label-width="labelWidth">
            <el-col :span="16">
              <!-- <el-select v-model="organization.types[0].text" placeholder="">
                <el-option v-for="item in organization.types" :key="item.id" :label="item.text" :value="item.text">
                </el-option>
              </el-select> -->
              <span style="font-size: 14px;color: #5A5A5A;margin-left: 6px">{{listData.industryName}}</span>
              <div class="select-type">
                <el-button v-for="(type, index) in selectedOrganizationType" size="small" :key="index">{{type}}
                  <i v-show="!editState.typeState" class="el-icon-circle-close el-icon--right" @click="deleteOrganizationType(index)"></i>
                </el-button>
              </div>
              <el-button v-if="selectedOrganizationType.length < childNumber" size='mini' border @click.prevent="addOrganizationType"></el-button>
              <el-button v-show="selectedOrganizationType.length > 0 && editState.typeState" type="text" @click.prevent="editState.typeState = false"></el-button>
              <el-button v-show="selectedOrganizationType.length > 0 && !editState.typeState" type="text" @click.prevent="editOrganizationType"></el-button>
            </el-col>
            <!-- <el-col :span="2">
              <el-button v-show="editState.typeState" type="text" @click.prevent="editState.typeState = false"></el-button>
              <el-button v-show="!editState.typeState" @click.prevent="editState.typeState = true"></el-button>
            </el-col> -->
            <el-col :span="8">
              <div class="el-form_item-tip"></div>
            </el-col>
          </el-form-item>
        </el-form>
        <el-form>
          <el-form-item label="" :label-width="labelWidth">
            <el-col :span="16">
              <el-cascader v-show="!editState.addrState" :options="city" :props="cascaderCityConf" change-on-select @change="cascaderCityChange"></el-cascader>
              <el-input v-show="!editState.addrState" class="editState" v-model="organization.addr"></el-input>
              <el-input v-show="editState.addrState" v-model="organization.addr" :disabled="true"></el-input>
              <el-button v-show="editState.addrState" type="text" @click.prevent="editState.addrState = false"></el-button>
              <el-button v-show="!editState.addrState" size="mini" @click.prevent="editOrganizationAddr"></el-button>
            </el-col>
            <el-col :span="8">
              <div class="el-form_item-tip"></div>
            </el-col>
          </el-form-item>
        </el-form>
        <el-form>
          <el-form-item label="" :label-width="labelWidth">
            <el-col :span="16">
              <el-input :class="{'editState': editState.descriptState == false}" v-model="organization.descript" type="textarea" :rows="3" resize="none" :disabled="editState.descriptState"></el-input>
              <el-button v-show="editState.descriptState" type="text" @click.prevent="editState.descriptState = false" style="vertical-align: top;"></el-button>
              <div v-show="!editState.descriptState">
                <el-button size="mini" @click.prevent="editOrganizationDescript"></el-button>
              </div>
            </el-col>
            <el-col :span="8">
              <div class="el-form_item-tip"></div>
            </el-col>
          </el-form-item>
        </el-form>
        <el-form>
          <el-form-item label="" :label-width="labelWidth">
            <el-col :span="16">
              <el-upload :action="uploadUrl" list-type="picture-card" :on-preview="handlePictureCardPreview" :on-remove="handleRemove">
                <i v-show="editState.qrcodeState" class="el-icon-plus" @click="editState.qrcodeState = false"></i>
                <el-button v-show="!editState.qrcodeState" size="mini" type="text"></el-button>
              </el-upload>
              <el-button v-show="!editState.qrcodeState" size="mini" @click.prevent="editState.qrcodeState = true"></el-button>
            </el-col>
            <el-col :span="8">
              <div class="el-form_item-tip"></div>
            </el-col>
          </el-form-item>
        </el-form>
        <el-form>
          <el-form-item label="" :label-width="labelWidth">
            <el-col :span="16">
              <el-input v-show="organization.stores.length == 0" placeholder="" :disabled="true"></el-input>
              <el-card v-show="organization.stores.length > 0" class="store-card" v-for="(storeItem, index) in organization.stores" :key="storeItem.id">
                <div slot="header" class="clearfix">
                  <span class="card-store-name">{{storeItem.name}}</span>
                  <i v-show="!editState.storesState" class="el-icon-close btn-remove-store" @click="removeStore(index)"></i>
                </div>
                <el-form :label-width="labelWidth" :rules="rules">
                  <el-form-item label="">
                    <span>{{storeItem.name}}</span>
                  </el-form-item>
                  <el-form-item label="" prop="phone">
                    <span>{{storeItem.phone}}</span>
                  </el-form-item>
                  <el-form-item label="">
                    <span>{{storeItem.linkMan}}</span>
                  </el-form-item>
                  <el-form-item label="">
                    <span>{{storeItem.addr}}</span>
                  </el-form-item>
                </el-form>
              </el-card>
              <div>
                <el-button type="text" @click.prevent="storeAddDialog = true"></el-button>
                <el-button v-show="editState.storesState && organization.stores.length > 0" type="text" @click.prevent="editState.storesState = false"></el-button>
                <el-button v-show="!editState.storesState && organization.stores.length > 0" size="mini" @click.prevent="editOrganizationStores"></el-button>
              </div>
            </el-col>
            <el-col :span="8">
              <div class="el-form_item-tip">5</div>
            </el-col>
          </el-form-item>
        </el-form>
      </div>
      <div class="dialog-modal">
        <el-dialog title="" :visible.sync="organizationTypeDialog">
          <div class="dialog-hd_label"></div>
          <div class="dialog-bd">
            <el-checkbox-group v-model="selectedOrganizationType">
              <el-checkbox-button v-for="(item, index) in childData" :label="item.name" round :key="item.id">{{item.name}}</el-checkbox-button>
            </el-checkbox-group>
          </div>
          <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="confirmOrganizationType"> </el-button>
          </div>
        </el-dialog>
        <el-dialog title="" :visible.sync="storeAddDialog">
          <el-form :model="store" :label-width="labelWidth" :rules="rules">
            <el-form-item label="">
              <el-input v-model="store.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="phone">
              <el-input v-model="store.phone"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="store.linkMan"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="store.addr"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="addStore"> </el-button>
          </div>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import CITY from 'src/lib/city.js'
import User from 'src/server/user'
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      labelWidth: '80px', // label-width
      editState: {
        // truefalse
        nameState: true,
        phoneState: true,
        typeState: true,
        addrState: true,
        descriptState: true,
        qrcodeState: true,
        storesState: true
      },
      listData: null,
      industryID: 100017,
      childData: null,
      childNumber: 0,
      selectedIndustry: null,
      organizationTypeDialog: false, // 
      storeAddDialog: false, // 
      organizationType: [
        {
          id: 1,
          text: '/'
        },
        {
          id: 2,
          text: ''
        },
        {
          id: 3,
          text: ''
        },
        {
          id: 4,
          text: ''
        },
        {
          id: 5,
          text: ''
        },
        {
          id: 6,
          text: ''
        },
        {
          id: 7,
          text: ''
        },
        {
          id: 8,
          text: ''
        },
        {
          id: 9,
          text: ''
        },
        {
          id: 10,
          text: ''
        },
        {
          id: 11,
          text: ''
        }
      ], // 
      selectedOrganizationType: [],
      organization: {
        name: '', // 
        phone: '18210123456', // 
        types: [
          {
            id: 1,
            text: ''
          }
        ], // 
        addr: '', // 
        descript: '', // 
        qrcode: '', // 
        stores: [] // 
      }, // 
      stores: [], // 
      store: {
        name: '', // 
        phone: '18210123456', // 
        linkMan: 'vins', // 
        addr: '' // 
      }, // 
      uploadUrl: 'https://jsonplaceholder.typicode.com/posts/', // 
      city: CITY, // js
      cascaderCityConf: {
        // 
        value: 'name',
        label: 'name',
        children: 'sub'
      },
      rules: {
        // 
        name: [
          {
            type: 'string',
            required: true,
            min: 0,
            max: 20,
            message: '20',
            trigger: 'change'
          }
        ],
        phone: [
          {
            required: true,
            pattern: /^((13[0-9]{1})|(14[0-9]{1})|(15[0-9]{1})|(17[0-9]{1})|(18[0-9]{1}))+\d{8}$/,
            message: '',
            trigger: 'change'
          }
        ]
      }
    }
  },
  computed: {
    ...mapGetters('user', {
      userInfo: 'userInfo'
    })
  },
  mounted () {
    User.industry().then(res => {
      // this.organizationType.=res.rs.subIndustry;
      // this.$store.commit("user/UPDATE_USERINFO", res.rs);
    })

    User.industryInfo().then(res => {
      console.log(res.rs)
      this.listData = res.rs
      this.industryID = res.rs.industry
      console.log('industryID1: ' + this.industryID)
      this.organization.name = res.rs.brandName
      this.organization.phone = res.rs.brandPhone
      this.organization.descript = res.rs.industryDesc
      this.organization.addr =
        res.rs.province + res.rs.city + res.rs.area + res.rs.street
      if (res.rs.subIndustry != null) {
        this.selectedIndustry = res.rs.subIndustry.split(',')
        console.log('selectedIndustry: ' + this.selectedIndustry)
        this.selectedIndustry.forEach(item => {
          if (item !== '') {
            this.selectedOrganizationType.push(item)
          }
        })
      }

      let params = {
        id: this.industryID
      }
      console.log('industryID2: ' + this.industryID)
      User.childIndustry({
        data: params
      }).then(res => {
        this.childData = res.rs
        this.childNumber = this.childData.length
      })
    })

    // this.$store.commit("user/UPDATE_USERINFO", res.rs);
    /* this.selectedOrganizationType.forEach((item,index) => {
      this.childData.forEach((item1) => {
        if (item1.name == item.name) {
        }
      })
    }) */
  },
  methods: {
    editOrganizationName () {
      // 
      this.editState.nameState = true
      // api
      User.industryInfo({
        data: {
          brandName: this.organization.name
        }
      }).then(res => {
        if (res.suc) {
          this.$message({
            message: '',
            type: 'success'
          })
          this.$store.commit('user/UPDATE_USERINFO', {
            brandName: this.organization.name
          })
        }
      })
    },
    editOrganizationPhone () {
      // 
      this.editState.phoneState = true
      // api
      User.industryInfo({
        data: {
          phone: this.organization.phone
        }
      }).then(res => {
        if (res.suc) {
          this.$message({
            message: '',
            type: 'success'
          })
          this.$store.commit('user/UPDATE_USERINFO', {
            phone: this.organization.phone
          })
        }
      })
    },
    editOrganizationAddr () {
      // 
      this.editState.addrState = true
      // api
    },
    editOrganizationDescript () {
      // 
      this.editState.descriptState = true
      // api
    },
    editOrganizationStores () {
      // 
      this.editState.storesState = true
      // api
    },
    confirmOrganizationType () {
      // 
      this.organizationTypeDialog = false
      this.editOrganizationType()
      console.log(this.selectedOrganizationType)
    },
    addOrganizationType () {
      // 
      this.organizationTypeDialog = true
    },
    deleteOrganizationType (index) {
      // 
      this.selectedOrganizationType.splice(index, 1)
      if (this.selectedOrganizationType === '') {
        this.editOrganizationType()
      }
      /* this.editOrganizationType() */
    },
    editOrganizationType () {
      // 
      let tmp = ''
      this.selectedOrganizationType.forEach((item, index) => {
        if (index === 0) {
          tmp = item
        } else {
          tmp = tmp + ',' + item
        }
      })
      console.log('tmp:' + tmp)
      let params = {
        subIndustry: tmp
      }
      User.update({
        data: params
      }).then(res => {
        /* console.log("subIndustry:"+subIndustry) */
      })
      this.editState.typeState = true
      // api
    },
    addStore () {
      // 
      this.storeAddDialog = false
      console.log(this.store)
      this.organization.stores.push(this.store)
    },
    removeStore (index) {
      // 
      this.organization.stores.splice(index, 1)
    },
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePictureCardPreview (file) {
      // 
      this.organization.qrcode = file.url
    },
    cascaderCityChange (val) {
      // 
      console.log(val)
    }
  }
}
</script>

<style lang="scss">
.box {
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  background: #fff;
  margin-top: 16px;
  margin-bottom: 20px;
  padding-bottom: 166px;
  .header-box {
    text-align: center;
    margin-top: 55px;
    margin-bottom: 70px;
    img {
      width: 92px;
      height: 92px;
      border-radius: 50%;
    }
  }
}
.stores-manage-wrap {
  .el-form-item__content {
    border-bottom: 1px solid #999;
  }
  .el-input,
  .el-textarea {
    display: inline-block;
    width: initial;
  }
  .el-textarea {
    width: 80%;
  }
  .el-input__inner {
    border: 1px solid #999;
    height: 1.8em;
  }
  .el-input.is-disabled .el-input__inner,
  .el-textarea.is-disabled .el-textarea__inner {
    background: transparent !important;
    border: none;
    cursor: text;
  }
}
.store-card {
  display: inline-block;
  width: calc((100% - 40px) / 2);
  min-width: 300px;
  margin-right: 20px;
  margin-bottom: 20px;
  box-shadow: none;
  .el-form-item__content {
    border: none;
  }
  .el-input {
    display: inline-block;
  }
  .el-input.is-disabled .el-input__inner {
    background: transparent !important;
    border: none;
    cursor: text;
  }
  .el-card__header {
    padding: 0 10px;
    color: #fc4a46;
  }
  .btn-remove-store {
    float: right;
    line-height: 40px;
  }
}

.nav-center-stores {
  .el-button--default {
    color: #fff;
    background-color: #fc4a46;
    border-color: #fc4a46;
  }
  .el-button--text {
    color: #fc4a46;
  }
  .el-checkbox-button {
    width: calc((100% - 60px) / 3);
    margin-top: 20px;
    margin-right: 20px;
  }
  .el-checkbox-button__inner {
    width: 100%;
    height: 40px;
    color: #979797;
    border: 1px solid #979797 !important;
    border-radius: 20px !important;
  }
  .el-checkbox-button.is-checked .el-checkbox-button__inner {
    color: #fff;
    background-color: #fc4a46 !important;
    border: 1px solid #fc4a46 !important;
    box-shadow: none;
  }
}
.dialog-modal {
  .el-input {
    width: 80%;
  }
}
</style>

<style scoped>
.stores-manage-wrap {
  padding: 0 50px;
}
.el-form_item-tip {
  text-align: right;
  color: #979797;
}
</style>



